import React from 'react';
import { Col, Form, InputGroup } from 'react-bootstrap';
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
interface Props {
  mdCol: string;
  label: string;
  name: string;
  values: any;
  onChange: (e: any) => void;
  isValid?: boolean;
  feedback?: any;
}

const PasswordInput = (props: Props) => {
  return (
    <Form.Group as={Col} md={props.mdCol || '6'} controlId="validationFormik01">
      <Form.Label>{props.label}</Form.Label>
      <InputGroup className="input-with-prefix">
        <LockOutlinedIcon className="text-muted icon-prefix" />
        <Form.Control
          style={{ paddingLeft: `36px` }}
          type="password"
          name={props.name}
          value={props.values}
          onChange={props.onChange}
          isValid={props.isValid}
        />
      </InputGroup>
      <Form.Control.Feedback>{props.feedback}</Form.Control.Feedback>
      <style scoped>{`
      .input-with-prefix {
        position: relative;
      }
      .icon-prefix {
        position: absolute;
        z-index: 99999;
        top: 6px;
        left: 8px

      }
      `}</style>
    </Form.Group>
  );
};

export default PasswordInput;
